import React from "react";
import ReactDOM from "react-dom";
import Appbar from "muicss/lib/react/appbar";
import Button from "muicss/lib/react/button";
import Container from "muicss/lib/react/container";
import Input from "muicss/lib/react/input";
import Radio from "muicss/lib/react/radio";
import Option from "muicss/lib/react/option";
import Select from "muicss/lib/react/select";
import { css } from "react-emotion";
// First way to import
import { ClipLoader } from "react-spinners";

const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
`;

class AwesomeComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true
    };
  }
  render() {
    return (
      <div className="sweet-loading">
        <ClipLoader
          className={override}
          sizeUnit={"px"}
          size={150}
          color={"#123abc"}
          loading={this.state.loading}
        />
      </div>
    );
  }
}

class Example extends React.Component {
  render() {
    return (
      <div>
        <Appbar />
        <AwesomeComponent />
        <Container>
          <div className="mui--text-display1">Payment Form</div>
          <br />
          <div className="mui--text-subhead">Select your plan</div>
          <br />
          <form>
            <Radio name="inputA" label="1 month" defaultChecked={true} />
            <Radio name="inputA" label="3 months" />
            <Radio name="inputA" label="6 months" />
            <Select
              name="input"
              label="Select the number cristals"
              defaultValue="option1"
            >
              <Option value="option1" label="2" />
              <Option value="option2" label="3" />
              <Option value="option3" label="4" />
              <Option value="option4" label="5" />
            </Select>
          </form>
          <div>
            <Input placeholder="Name" defaultValue="Name" />
            <Input placeholder="Last Name" defaultValue="Last Name" />
          </div>
          <div>
            <h1>Heading 1</h1>
            <h2>Heading 2</h2>
            <h3>Heading 3</h3>
            <h4>Heading 4</h4>
            <h5>Heading 5</h5>
            <h6>Heading 6</h6>
            <hr />
            <div>
              <a href="#">Links</a>
            </div>
            <div>
              <strong>Strong</strong>
            </div>
            <div>
              <em>Emphasis</em>
            </div>
            <div>
              <abbr title="Abbreviation">Abbr</abbr>
            </div>
          </div>
          <Button color="primary">Send</Button>
        </Container>
      </div>
    );
  }
}

ReactDOM.render(<Example />, document.getElementById("root"));
